.checkout-item {
  width: 100%;
  display: flex;
  min-height: 100px;
  border-bottom: 1px solid darkgrey;
  padding: 15px 0;
  font-size: 20px;
  align-items: center;
}
.checkout-item div {
  margin-right: 40px;
}
.image-container {
  width: 10%;
}
img {
  width: 100%;
  height: 100%;
}
.name,
.quantity,
.price,
.size,
.color {
  width: 23%;
}

.quantity {
  display: flex;
}
.value {
  margin: 0 10px;
}

.remove-button {
  padding-left: 12px;
  cursor: pointer;
}
